'use client'

import { useEffect, useState, CSSProperties } from 'react'
import {
  Plus,
  Pencil,
  Trash2,
} from 'lucide-react'
import CrudLayout from '../../components/CrudLayout'
import CrudTable from '../../components/CrudTable'
import CrudPagination from '../../components/CrudPagination'
import { api } from '@/lib/api'

type Usuario = {
  user_id: string
  nome: string
  email: string
  perfil: 'Administrador' | 'Usuario'
  ativo: 'Sim' | 'N√£o'
}

export default function UsuariosPage() {
  const [dados, setDados] = useState<Usuario[]>([])
  const [busca, setBusca] = useState('')
  const [page, setPage] = useState(1)
  const [total, setTotal] = useState(0)

  const [open, setOpen] = useState(false)
  const [editando, setEditando] = useState(false)
  const [confirmar, setConfirmar] = useState<Usuario | null>(null)

  const [form, setForm] = useState<any>({
    user_id: '',
    nome: '',
    email: '',
    senha: '',
    perfil: 'Usuario',
    ativo: 'Sim',
  })

  const pageSize = 5

  async function carregar() {
    const res = await api.get(
      `/entity/usuarios?page=${page}&limit=${pageSize}&search=${busca}`,
    )
    setDados(res.data)
    setTotal(res.total)
  }

  useEffect(() => {
    carregar()
  }, [page, busca])

  function novoUsuario() {
    setForm({
      user_id: '',
      nome: '',
      email: '',
      senha: '',
      perfil: 'Usuario',
      ativo: 'Sim',
    })
    setEditando(false)
    setOpen(true)
  }

  function editarUsuario(usuario: Usuario) {
    setForm(usuario)
    setEditando(true)
    setOpen(true)
  }

  async function salvar() {
    if (!form.email || form.email.trim() === '') {
      alert('O campo E-mail √© obrigat√≥rio.')
      return
    }

    if (editando) {
      await api.put(`/entity/usuarios/${form.user_id}`, form)
    } else {
      await api.post('/entity/usuarios', form)
    }

    setOpen(false)
    carregar()
  }

  async function excluir() {
    if (!confirmar) return
    await api.put(`/entity/usuarios/${confirmar.user_id}/excluir`, {})
    setConfirmar(null)
    carregar()
  }

  return (
    <CrudLayout
      title="Funcion√°rios/Usu√°rios"
      subtitle="Gerencie os funcion√°rios/usu√°rios do sistema"
      actions={
        <div style={actions}>
          <button style={btnPrimary} onClick={novoUsuario}>
            <Plus size={16} /> Novo usu√°rio
          </button>
        </div>
      }
    >
      <input
        placeholder="Buscar usu√°rio..."
        value={busca}
        onChange={e => {
          setBusca(e.target.value)
          setPage(1)
        }}
        style={search}
      />

      <CrudTable
        columns={[
          { key: 'nome', label: 'Nome' },
          { key: 'email', label: 'E-mail' },
          {
            key: 'ativo',
            label: 'Status',
            render: u => (
              <span style={u.ativo === 'Sim' ? badgeAtivo : badgeInativo}>
                {u.ativo}
              </span>
            ),
          },
          {
            key: 'actions',
            label: 'A√ß√µes',
            render: u => (
              <div style={{ display: 'flex', gap: 6 }}>
                <button style={btnIcon} onClick={() => editarUsuario(u)}>
                  <Pencil size={14} />
                </button>
                <button style={btnDelete} onClick={() => setConfirmar(u)}>
                  <Trash2 size={14} />
                </button>
              </div>
            ),
          },
        ]}
        data={dados}
      />

      <CrudPagination
        page={page}
        total={total}
        pageSize={pageSize}
        onPageChange={setPage}
      />

      {confirmar && (
        <div style={overlay}>
          <div style={modal}>
            <h3>Confirmar exclus√£o</h3>
            <p>
              Deseja remover o acesso do usu√°rio{' '}
              <strong>{confirmar.nome}</strong>?
            </p>

            <div style={modalFooter}>
              <button style={btnCancel} onClick={() => setConfirmar(null)}>
                Cancelar
              </button>
              <button style={btnDelete} onClick={excluir}>
                Confirmar
              </button>
            </div>
          </div>
        </div>
      )}

      {open && (
        <div style={overlay}>
          <div style={modal}>
            <h3>{editando ? 'Editar usu√°rio' : 'Novo usu√°rio'}</h3>

            <input
              placeholder="Usu√°rio"
              value={form.user_id}
              disabled={editando}
              onChange={e => setForm({ ...form, user_id: e.target.value })}
              style={input}
            />

            <input
              placeholder="Nome"
              value={form.nome}
              onChange={e => setForm({ ...form, nome: e.target.value })}
              style={input}
            />

            <input
              placeholder="E-mail *"
              value={form.email}
              onChange={e => setForm({ ...form, email: e.target.value })}
              style={input}
            />

            {!editando && (
              <input
                placeholder="Senha"
                type="password"
                onChange={e => setForm({ ...form, senha: e.target.value })}
                style={input}
              />
            )}

            <div style={modalFooter}>
              <button style={btnCancel} onClick={() => setOpen(false)}>
                Cancelar
              </button>
              <button style={btnPrimary} onClick={salvar}>
                Salvar
              </button>
            </div>
          </div>
        </div>
      )}
    </CrudLayout>
  )
}

/* ===== STYLES ===== */

const search: CSSProperties = {
  width: 280,
  padding: '8px 12px',
  borderRadius: 6,
  border: '1px solid #d1d5db',
  marginBottom: 16,
}

const actions: CSSProperties = {
  display: 'flex',
  gap: 10,
}

const btnPrimary: CSSProperties = {
  background: '#16a34a',
  color: '#fff',
  padding: '8px 14px',
  borderRadius: 6,
  border: 'none',
}

const btnCancel: CSSProperties = {
  background: '#e5e7eb',
  padding: '8px 14px',
  borderRadius: 6,
  border: 'none',
}

const btnIcon: CSSProperties = {
  background: '#e5e7eb',
  border: 'none',
  borderRadius: 6,
  padding: 6,
}

const btnDelete: CSSProperties = {
  background: '#dc2626',
  color: '#fff',
  border: 'none',
  borderRadius: 6,
  padding: '6px 10px',
}

const badgeAtivo: CSSProperties = {
  background: '#e7f6ec',
  color: '#1e7e34',
  padding: '4px 10px',
  borderRadius: 12,
}

const badgeInativo: CSSProperties = {
  background: '#fdecea',
  color: '#c62828',
  padding: '4px 10px',
  borderRadius: 12,
}

const overlay: CSSProperties = {
  position: 'fixed',
  inset: 0,
  background: 'rgba(0,0,0,0.4)',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
}

const modal: CSSProperties = {
  background: '#fff',
  borderRadius: 12,
  width: 420,
  padding: 24,
}

const input: CSSProperties = {
  padding: '10px 12px',
  borderRadius: 6,
  border: '1px solid #d1d5db',
}

const modalFooter: CSSProperties = {
  display: 'flex',
  justifyContent: 'flex-end',
  gap: 10,
  marginTop: 20,
}
